.sidebar .sidebar-row .sidebar-content .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar { background-color: transparent; }

.sidebar .sidebar-row .sidebar-content .mCSB_scrollTools .mCSB_draggerRail { background-color: transparent; }

.sidebar .sidebar-row .sidebar-content ul { padding-left: 30px; list-style-type: none; }

.sidebar .sidebar-row .sidebar-content ul li { margin-bottom: 15px; }

.sidebar .sidebar-row .sidebar-content ul li:first-child a { color: #39a6be; }

.sidebar .sidebar-row .sidebar-content ul li a { color: #444; }

.sidebar .apply-filter-button { text-align: center; width: 100%; color: #39a6be; font-family: Arial, sans-serif; font-size: 13px; display: block; padding: 13px 0; }

.content-block { width: 74.4%; height: 100%; }

.content-block .items-container { display: flex; flex-wrap: wrap; justify-content: space-between; }

.content-block .items-container .item-card { padding-bottom: 18px; font-size: 15px; cursor: pointer; width: 31%; font-family: "Open Sans", sans-serif; text-align: center; border: 1px solid #e1e1e1; border-radius: 4px; box-shadow: 0 1px 2.8px 1.2px rgba(0, 0, 0, 0.09); background-color: #FFF; transition: all 600ms cubic-bezier(0.25, 0.46, 0.45, 0.94); margin-bottom: 8px; }

.content-block .items-container .item-card:hover { border: 1px solid #c2e8f0; box-shadow: 0 5px 10.6px 1.4px rgba(68, 190, 217, 0.36); }

.content-block .items-container .item-card:hover .title-card { color: #23a2be; }

.content-block .items-container .item-card .img-card { margin-bottom: 25px; position: relative; }

.content-block .items-container .item-card .img-card img { max-width: 100%; }

.content-block .items-container .item-card .img-card .desc-card { text-transform: uppercase; position: absolute; top: 15px; cursor: default; font-weight: 600; left: 15px; color: #fff; padding: 8px 5px; border-radius: 3px; font-size: 11px; font-family: "Open Sans", sans-serif; }

.content-block .items-container .item-card .img-card .desc-card_new { background-color: #48B3CB; }

.content-block .items-container .item-card .img-card .desc-card_hit-sales { background-color: #ffae2e; }

.content-block .items-container .item-card .rate-card { margin-bottom: 20px; }

.content-block .items-container .item-card .title-card { margin-bottom: 40px; transition: all 600ms cubic-bezier(0.25, 0.46, 0.45, 0.94); }

.content-block .items-container .item-card .price-card { position: relative; font-weight: bold; }

.content-block .items-container .item-card .price-card:before { content: ""; position: absolute; left: 35%; top: -20px; width: 79px; height: 1px; background-color: #dbdbdb; }

.content-block .items-content { padding-bottom: 200px; }

.content-block .items-content .other-items-container { align-items: center; justify-content: center; }

.content-block .items-content .other-items-container .slick-list { margin-bottom: 52px; }

.content-block .items-content .other-items-container .item-card { width: 270px !important; margin-left: 10px; margin-right: 10px; }

.content-block .items-content .other-items-container .item-card .rate-card { display: flex; -webkit-justify-content: center; justify-content: center; }

.content-block .items-content .other-items-container .slick-track { display: flex; }

.content-block .items-content .other-items-container .parent-arrow { position: relative; display: flex; justify-content: center; align-items: center; cursor: pointer; }

.content-block .items-content .other-items-container .parent-arrow:before { content: ""; border: 1px solid #d7d7d7; position: absolute; width: 38px; height: 38px; background-color: #f1f1f1; border-radius: 50%; }

.content-block .items-content .other-items-container .parent-arrow img { width: 6px; height: 10px; position: relative; }

.content-block .items-content .other-items-container .slick-prev { order: 1; }

.content-block .items-content .other-items-container .slick-dots { margin: 0 30px; list-style-type: none; order: 3; display: flex; width: 50px; justify-content: space-between; }

.content-block .items-content .other-items-container .slick-dots li { position: relative; width: 5px; border-radius: 50%; height: 5px; margin-right: 5px; background-color: #ccc; }

.content-block .items-content .other-items-container .slick-dots .slick-active { background-color: #e44256; }

.content-block .items-content .other-items-container .slick-dots button { display: none; }

.content-block .items-content .other-items-container .slick-next { order: 4; }

.content-block .items-content .items-title { color: #282828; font-family: "Open Sans", sans-serif; font-size: 16px; text-transform: uppercase; }

@media screen and (max-width: 1200px) { .main-wrapper { padding: 0 10px; } }

@media screen and (max-width: 1100px) { .sidebar .sidebar-row .sidebar-title .sidebar-filter { width: 60%; } }

@media screen and (max-width: 992px) { .content-container { flex-direction: column; }
  .content .wrapper { width: 88%; margin: 0 auto; }
  .sidebar { margin-right: 0; width: 30%; margin-bottom: 20px; }
  .content-block { width: 100%; } }

@media screen and (max-width: 767px) { .sidebar { width: 40%; } }

@media screen and (max-width: 600px) { .sidebar { width: 55%; }
  .content-block .items-container .item-card { margin-bottom: 30px; flex-basis: 45%; } }

@media screen and (max-width: 450px) { .content-block .items-container .item-card .img-card img { width: 100%; }
  .sidebar { width: 100%; }
  .content-block .items-container .item-card { flex-basis: 100%; } }

@media screen and (max-width: 340px) { .sidebar { width: 95%; }
  .content-block .items-container .item-card { flex-basis: 95%; }
  .content-block .items-content .other-items-container .item-card { margin-left: 0; margin-right: 19px; } }
